<template>
  <div :class="className" :style="{height:height,width:width}"/>
</template>

<script>
import echarts from "echarts";
require("echarts/theme/macarons"); // echarts theme
import { debounce } from "@/utils";

export default {
  props: {
    className: {
      type: String,
      default: "chart"
    },
    width: {
      type: String,
      default: "100%"
    },
    height: {
      type: String,
      default: "350px"
    },
    autoResize: {
      type: Boolean,
      default: true
    },
    chartData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      chart: null
    };
  },
  watch: {
    chartData: {
      deep: true,
      handler(val) {
        this.setOptions(val);
      }
    }
  },
  mounted() {
    this.initChart();
    if (this.autoResize) {
      this.__resizeHandler = debounce(() => {
        if (this.chart) {
          this.chart.resize();
        }
      }, 100);
      window.addEventListener("resize", this.__resizeHandler);
    }

    // 监听侧边栏的变化
    const sidebarElm = document.getElementsByClassName("sidebar-container")[0];
    sidebarElm.addEventListener("transitionend", this.sidebarResizeHandler);
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    if (this.autoResize) {
      window.removeEventListener("resize", this.__resizeHandler);
    }

    const sidebarElm = document.getElementsByClassName("sidebar-container")[0];
    sidebarElm.removeEventListener("transitionend", this.sidebarResizeHandler);

    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    sidebarResizeHandler(e) {
      if (e.propertyName === "width") {
        this.__resizeHandler();
      }
    },
    dataForm() {},
    setOptions(val) {
      let  date=[];
      let  formData=[[],[],[],[]];
      for (let key in val) {
        console.log(key);
        date.push(key);
        formData[0].push(val[key]['1']);
        formData[1].push(val[key]['2']);
        formData[2].push(val[key]['3']);
        formData[3].push(val[key]['4']);
      }
      this.chart.setOption({
        xAxis: {
          data: date,
          boundaryGap: false,
          axisTick: {
            show: false
          },
           axisPointer: {
              label: {
              }
          },
        },
        grid: {
          left: 10,
          right: 20,
          bottom: 20,
          top: 30,
          containLabel: true
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross"
          },
          padding: [5, 10]
        },
        yAxis: {
          axisTick: {
            show: false
          },
        },
        legend: {
          data: ["成功", "转换中", "待转换", "失败"]
        },
        series: [
          {
            name: "成功",
            itemStyle: {
              normal: {
                color: "#3888fa",
                lineStyle: {
                  color: "#3888fa",
                  width: 2
                }
              }
            },
            smooth: true,
            type: "line",
            data: formData[0],
            animationDuration: 2800,
            animationEasing: "cubicInOut"
          },
          {
            name: "转换中",
            smooth: true,
            type: "line",
            itemStyle: {
              normal: {
                color: "#f80",
                lineStyle: {
                  color: "#f80",
                  width: 2
                },
                areaStyle: {
                  color: "#f3f8ff"
                }
              }
            },
            data: formData[1],
            animationDuration: 2800,
            animationEasing: "quadraticOut"
          },
          {
            name: "待转换",
            itemStyle: {
              normal: {
                color: "#666",
                lineStyle: {
                  color: "#666",
                  width: 2
                }
              }
            },
            smooth: true,
            type: "line",
            data: formData[2],
            animationDuration: 2800,
            animationEasing: "cubicInOut"
          },
          {
            name: "失败",
            smooth: true,
            type: "line",
            itemStyle: {
              normal: {
                color: "#FF005A",
                lineStyle: {
                  color: "#FF005A",
                  width: 2
                },
                areaStyle: {
                  color: "#f3f8ff"
                }
              }
            },
            data: formData[3],
            animationDuration: 2800,
            animationEasing: "quadraticOut"
          }
        ]
      });
    },
    initChart() {
      this.chart = echarts.init(this.$el, "macarons");
      this.setOptions(this.chartData);
    }
  }
};
</script>
