<style lang="scss">
.home {
  &_dataCount {
    .el-card__header {
      .clearfix {
        display: flex;
        justify-content: space-between;
        align-items: center;
        &:after {
          display: none;
        }
      }
    }
  }
}
.taskMessage{
  text-align: center;
 
}
.datacount_pptmsg {
  margin: 0px 15px 15px 0;
}
</style>

<template>
  <div class="dashboard-editor-container">
    <el-row style="margin-bottom:20px;" v-loading="lineLoading">
      <el-card class="box-card home_dataCount">
        <div slot="header" class="clearfix">
          <span>状态统计</span>
          <el-switch v-model="update" inactive-text="定时更新" disabled></el-switch>
          <div>
            <el-date-picker
              v-model="currentDay"
              type="date"
              @change="dayChange"
              value-format="timestamp"
              placeholder="选择日期"
            ></el-date-picker>
            <el-select @change="timeChange" v-model="currentTime" placeholder="请选择时间段">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
        </div>
        <div class="chart-wrapper">
          <line-chart :chart-data="lineChartData"/>
          <el-row :gutter="20">
            <el-col :span="8">
              <pie-chart :dataList="dataList"/>
            </el-col>
            <el-col :span="16">
              <bar-chart :topList="topList"/>
            </el-col>
          </el-row>
          <el-table :data="errorData" border stripe max-height="250" highlight-current-row>
            <el-table-column prop="createdOn" align="center" label="转换时间">
              <template slot-scope="scope">
                <span>{{ scope.row.createdOn | parseTime('{y}-{m}-{d} {h}:{i}') }}</span>
              </template>
            </el-table-column>
            <el-table-column prop="taskId" align="center" label="PPT任务编号"></el-table-column>
            <el-table-column label="状态" align="center" width="150">
              <template slot-scope="scope">
                <el-tag type="danger">失败</el-tag>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-card>
    </el-row>

    <el-row :gutter="8">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>查询ppt转化进度</span>
        </div>
        <el-row style="margin-bottom:15px;">
          <el-input
            style="width:500px"
            placeholder="请输入任务taskId或pptUrl"
            v-model="searchValue"
            clearable
          ></el-input>
          <el-button
            style="margin-left:20px"
            type="primary"
            @click="searchProcess"
            icon="el-icon-search"
            circle
          ></el-button>
        </el-row>
        <el-row style="margin:20px 0">
          <el-col class="taskMessage" :span="4">
            <el-progress v-if="currentType.taskStatus==3" type="circle" :percentage="0"></el-progress>
            <el-progress v-if="currentType.taskStatus==2" type="circle" :percentage="50"></el-progress>
            <el-progress
              v-if="currentType.taskStatus==1"
              type="circle"
              :percentage="100"
              status="success"
            ></el-progress>
            <el-progress
              v-if="currentType.taskStatus==4"
              type="circle"
              :percentage="50"
              status="exception"
            ></el-progress>
          </el-col>
          <el-col :span="20" v-show="currentType.taskStatus">
            <el-tag
              class="datacount_pptmsg"
            >状态: {{currentType.taskStatus==1?"完成":currentType.taskStatus==2?"转换中":currentType.taskStatus==3?"待转换":"失败"}}</el-tag>
            <el-tag
              class="datacount_pptmsg"
            >开始时间: {{currentType.startTime | parseTime('{y}-{m}-{d} {h}:{i}:{s}')}}</el-tag>
            <el-tag
              class="datacount_pptmsg"
            >结束时间: {{currentType.endTime | parseTime('{y}-{m}-{d} {h}:{i}:{s}')}}</el-tag>
            <el-tag class="datacount_pptmsg" type="success">PPT地址: {{currentType.pptUrl}}</el-tag>
            <el-tag class="datacount_pptmsg" type="success">ZIP地址: {{currentType.zipUrl}}</el-tag>
          </el-col>
          <el-col style="margin-top:20px" :span="24" v-show="currentType.taskStatus">
            <tree-view :data="currentType"></tree-view>
          </el-col>
        </el-row>

        <el-button v-show="currentType.taskStatus==4" type="primary">重新上传</el-button>
      </el-card>
    </el-row>
  </div>
</template>

<script>
import { Home } from "@/api/home";
import LineChart from "./components/LineChart";
// import RaddarChart from "./components/RaddarChart";
import PieChart from "./components/PieChart";
import BarChart from "./components/BarChart";
import TransactionTable from "./components/TransactionTable";
import TodoList from "./components/TodoList";
import BoxCard from "./components/BoxCard";

export default {
  name: "DashboardAdmin",
  components: {
    LineChart,
    // RaddarChart,
    PieChart,
    BarChart,
    TransactionTable,
    TodoList,
    BoxCard
  },
  data() {
    return {
      errorData: [],
      lineLoading: false,
      lineChartData: {},
      currentType: {},
      currentDay: new Date( // 当天23:59
        new Date(new Date().toLocaleDateString()).getTime()
      ).getTime(),
      inter: null,
      currentTime: "2", //当前时间段
      options: [
        {
          value: "5",
          label: "一年"
        },
        {
          value: "0",
          label: "一个月"
        },
        {
          value: "1",
          label: "一周"
        },
        {
          value: "2",
          label: "一天"
        },
        {
          value: "3",
          label: "一小时"
        },
        {
          value: "4",
          label: "一分钟"
        }
      ],
      topList: [],
      dataList: [0, 0, 0, 0],
      searchValue: "",
      update: true
    };
  },
  created() {
    clearInterval(this.inter);
    this.timeChange("0");
  },
  beforeDestroy() {
    clearInterval(this.inter);
  },
  methods: {
    /**
     * @description 获取统计数据
     * @function getCountData
     */
    getCountData(data) {},
    /**
     * @description 日期格式化
     * @function dataForm
     * @return number 格式化后的时间
     */
    dataForm(type, time) {
      let dateObj = new Date(time);
      return type === "0"
        ? dateObj.getDate() + "日"
        : type === "1"
        ? dateObj.getDate() + "日"
        : type === "2"
        ? dateObj.getHours() + "时"
        : type === "3"
        ? dateObj.getMinutes() + "分"
        : type === "4"
        ? dateObj.getSeconds() + "秒"
        : dateObj.getMonth() + 1 + "月";
    },
    /**
     * @description 时间更新
     * @function timeChange
     * @return {type} {description}
     */
    timeChange() {
      this.lineLoading = true;
      Home.statistical({
        ...this.timeForm(this.currentTime)
      }).then(res => {
        this.dataList = [0, 0, 0, 0];
        this.topList = [];
        for (let index = 0; index < 24; index++) {
          this.topList[index] = 0;
        }
        let arrayList = {},
          errData = [];
        for (let item of res.data) {
          let objKey = this.dataForm(this.currentTime, item.createdOn);
          if (!arrayList[objKey]) {
            arrayList[objKey] = {
              "1": 0,
              "2": 0,
              "3": 0,
              "4": 0
            };
          }
          // console.log(new Date(item.createdOn).getHours());
          this.topList[new Date(item.createdOn).getHours()] += 1;
          switch (item.taskStatus) {
            case 1:
              this.dataList[0]++;
              break;
            case 2:
              this.dataList[1]++;
              break;
            case 3:
              this.dataList[2]++;
              break;
            default:
              this.dataList[3]++;
              errData.push(item);
              break;
          }

          arrayList[objKey][item.taskStatus] += 1;
        }
        this.errorData = errData;
        this.lineChartData = arrayList;
        this.lineLoading = false;
        // 4：转换失败 3：待转换  2：转换中  1：转换完成
      });
      clearInterval(this.inter);
      if (this.currentTime - 0 >= 2) {
        this.update = true;
        this.inter = setInterval(() => {
          this.timeChange();
        }, 5 * 60 * 1000);
      } else {
        this.update = false;
      }
    },
    dayChange(val) {
      console.log(val);
      this.timeChange();
    },
    /**
     * @description 时间格式化
     * @function timeForm
     */
    timeForm(type) {
      let beginTime = 0,
        interval = 0,
        endTime = 0;
      switch (type) {
        case "0":
          interval = 60 * 1000 * 60 * 24 * 30;
          break;

        case "1":
          interval = 60 * 1000 * 60 * 24 * 7;
          break;

        case "2":
          interval = 60 * 1000 * 60 * 24;
          break;

        case "3":
          interval = 60 * 1000 * 60;
          break;

        case "5":
          interval = 60 * 1000 * 60 * 24 * 30 * 12;
          break;

        default:
          interval = 60 * 1000;
          break;
      }
      endTime = new Date().getTime();
      if (this.currentDay < endTime) {
        endTime = this.currentDay + 60 * 1000 * 60 * 24;
      }
      if (new Date(this.currentDay).getDate() == new Date().getDate()) {
        endTime = new Date().getTime();
      }
      // return {
      //   beginTime: 1448000307000,
      //   endTime: 1542694707000
      // };
      return {
        endTime,
        beginTime: endTime - interval
      };
    },
    /**
     * @description 手动上传
     * @function reUpload
     */
    reUpload(taskId) {
      Home.reConvertFailTask({
        taskId
      }).then(res => {
        this.$message.success("成功上传");
      });
    },
    /**
     * @function searchProcess
     */
    searchProcess() {
      if (!this.searchValue) {
        return false;
      }
      Home.getPptConvertProcessing({
        fileId: this.searchValue
      }).then(res => {
        this.currentType = res.data[0];
      });
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.dashboard-editor-container {
  padding: 32px;
  background-color: rgb(240, 242, 245);
  .chart-wrapper {
    background: #fff;
    padding: 16px 16px 0;
    margin-bottom: 32px;
  }
}
</style>
