<template>
  <div :class="className" :style="{height:height,width:width}"/>
</template>

<script>
import echarts from "echarts";
require("echarts/theme/macarons"); // echarts theme
import { debounce } from "@/utils";

export default {
  props: {
    className: {
      type: String,
      default: "chart"
    },
    width: {
      type: String,
      default: "100%"
    },
    height: {
      type: String,
      default: "400px"
    },
    dataList: {
      type: Array,
      default: () => {
        return [0,0,0,0];
      }
    }
  },
  watch: {
    dataList: {
      deep: true,
      handler(val) {
        this.setOptions(val);
      }
    }
  },
  data() {
    return {
      chart: null
    };
  },
  mounted() {
    this.initChart();
    this.__resizeHandler = debounce(() => {
      if (this.chart) {
        this.chart.resize();
      }
    }, 100);
    window.addEventListener("resize", this.__resizeHandler);
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    window.removeEventListener("resize", this.__resizeHandler);
    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    setOptions(val) {
      this.chart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          left: "center",
          bottom: "10",
          data: ["成功", "转换中", "待转换", "失败"]
        },
        calculable: true,
        series: [
          {
            name: "当前转换状态统计",
            color: ['#3888fa', '#67c23a', '#f80', '#FF005A', '#FFCC67'],
            type: "pie",
            roseType: "radius",
            radius: [55, 105],
            center: ["50%", "50%"],
            data: [
              { value: val[0], name: "成功" },
              { value: val[1], name: "转换中" },
              { value: val[2], name: "待转换" },
              { value: val[3], name: "失败" }
            ],
            animationEasing: "cubicInOut",
            animationDuration: 2600
          }
        ]
      });
    },
    initChart() {
      this.chart = echarts.init(this.$el, "macarons");
      this.setOptions(this.dataList);
      
    }
  }
};
</script>
